




































import { Component, Vue } from 'vue-property-decorator';
import { Slider, Header, Loading } from '../components';
import { getTrackInfo } from '../api';

@Component({
  components: {
    Slider,
    YHeader: Header,
    Loading,
  },
  name: 'player',
})
export default class Player extends Vue {
  private created() {
    this.loadData();
  }

  private data() {
    return {
      data: null,
    };
  }

  get currentMusic() {
    return this.$store.state.player.musicList[this.$store.state.player.currentMusicIndex];
  }

  get isPlay() {
    return this.$store.state.player.isPlay;
  }

  get duration() {
    return this.$store.state.player.duration;
  }

  get currentTime() {
    return this.$store.state.player.currentTime;
  }

  private slideChange(e: number) {
    this.$store.commit('player/play');
    this.$store.commit('player/setSeeked', e);
  }

  private play() {
    this.$store.commit('player/add', this.data);
  }

  private pause() {
    this.$store.commit('player/pause');
  }

  private goBack() {
    this.$router.back();
  }

  private async loadData() {
    const res = await getTrackInfo(+this.$route.params.id);
    this.data = res;
    const test = res.lyrics;
  }
}
